// 公司
import React from 'react';
import {Route,Link} from 'react-router-dom';
import Header from './component/header';
import Footer from './component/footer';
import Introduce from './component/introduce';
import Cooprator from './component/cooprator';
import Recruit from './component/recruit';
import Connect from './component/connect';
import Download from './component/download';
import Law from './component/law';
import Payment from './component/payment';
import './../css/company.css';
export default class Company extends React.Component{
    componentDidMount(){
        const companylist=document.getElementById('companylist');
        const oLis=companylist.getElementsByTagName('a');
        for(let i=0; i<oLis.length; i++){
            oLis[i].onclick=function(){
                for(let i=0; i<oLis.length; i++){
                    oLis[i].className="";
                }
                this.className="active";
            }
        }
    }
    render(){
        return(
            <div>
                {/*头部开始*/}
                <Header/>
                {/*头部结束*/}
                {/*内容开始*/}
                <div id="company" className="w">
                    {/*左边列表开始*/}
                    <div className="lists">
                        <h1>关于我们</h1>
                        <div className="listwrapper" id="companylist">
                            <Link to="/company" className="active">乐活介绍<i></i></Link>
                            <Link to="/company/cooprator">合作伙伴<i></i></Link>
                            <Link to="/company/recruit">招聘信息<i></i></Link>
                            <Link to="/company/connect">联系我们<i></i></Link>
                            <Link to="/company/download">App下载<i></i></Link>
                            <Link to="/company/law">法律法规<i></i></Link>
                            <Link to="/company/payment">支付信息<i></i></Link>
                        </div>
                    </div>
                    {/*左边列表结束*/}
                    {/*右边内容开始*/}
                    <div className="content">
                        <Route exact path="/company" component={Introduce}/>
                        <Route path="/company/cooprator" component={Cooprator}/>
                        <Route path="/company/recruit" component={Recruit}/>
                        <Route path="/company/connect" component={Connect}/>
                        <Route path="/company/download" component={Download}/>
                        <Route path="/company/law" component={Law}/>
                        <Route path="/company/payment" component={Payment}/>
                    </div>
                    {/*右边内容结束*/}
                </div>
                {/*内容结束*/}
                {/*尾部开始*/}
                <Footer/>
                {/*尾部结束*/}
            </div>
        );
    };
}


