//个人中心
import React from 'react';
import './../../css/orders.css';

import booking from './../../images/booking.png';
import yilong from './../../images/yilong.png';
import xiecheng from './../../images/xiecheng.png';
import agoda from './../../images/agoda.png';
import tongcheng from './../../images/tongcheng.jpg';
import lvmama from './../../images/lvmama.png';
import qunaer from './../../images/qunaer.jpg';
import getyourguide from './../../images/getyourguide.png';
import feizhu from './../../images/feizhu.jpg';
import tuniu from './../../images/tuniu.jpg';
import haoding from './../../images/haoding.jpg';
import meituan from './../../images/meituan.jpg';
import xiaozhu from './../../images/xiaozhu.jpg';
import tujia from './../../images/tujia.jpg';
import botao from '../../images/botao.jpg';
import viator from './../../images/viator.jpg';

export default class Orders extends React.Component{
    render(){
        return(
            <div id="orders">
                <a href="https://secure.booking.com/myreservations.zh-cn.html">
                    <img src={booking} alt=""/>
                    <span>Booking</span>
                    <i></i>
                </a>
                <a href="https://secure.elong.com/passport/login_cn.html">
                    <img src={yilong} alt=""/>
                    <span>艺龙</span>
                    <i></i>
                </a>
                <a href="https://passport.ctrip.com/user/login">
                    <img src={xiecheng} alt=""/>
                    <span>携程</span>
                    <i></i>
                </a>
                <a href="https://www.agoda.com/zh-cn/account/signin.html?">
                    <img src={agoda} alt=""/>
                    <span>安可达</span>
                    <i></i>
                </a>
                <a href="https://passport.ly.com">
                    <img src={tongcheng} alt=""/>
                    <span>同程旅游</span>
                    <i></i>
                </a>
                <a href="https://login.lvmama.com/nsso/login">
                    <img src={lvmama} alt=""/>
                    <span>驴妈妈</span>
                    <i></i>
                </a>
                <a href="https://user.qunar.com/passport/login.jsp">
                    <img src={qunaer} alt=""/>
                    <span>去哪儿</span>
                    <i></i>
                </a>
                <a href="https://www.getyourguide.com/zh/login">
                    <img src={getyourguide} alt=""/>
                    <span>getyourguide</span>
                    <i></i>
                </a>
                <a href="https://login.taobao.com/member/login.jhtml">
                    <img src={feizhu} alt=""/>
                    <span>飞猪</span>
                    <i></i>
                </a>
                <a href="https://passport.tuniu.com/login/index">
                    <img src={tuniu} alt=""/>
                    <span>途牛</span>
                    <i></i>
                </a>
                <a href="https://www.hotels.cn/profile/signin.html">
                    <img src={haoding} alt=""/>
                    <span>好订网</span>
                    <i></i>
                </a>
                <a href="https://passport.meituan.com/account/unitivelogin?backurl=https%3A%2F%2Fi.meituan.com%2Faccount">
                    <img src={meituan} alt=""/>
                    <span>美团网</span>
                    <i></i>
                </a>
                <a href="http://www.xiaozhu.com/">
                    <img src={xiaozhu} alt=""/>
                    <span>小猪</span>
                    <i></i>
                </a>
                <a href="https://passport.tujia.com/PortalSite/LoginPage/">
                    <img src={tujia} alt=""/>
                    <span>途家</span>
                    <i></i>
                </a>
                <a href="http://www.plateno.com/">
                    <img src={botao} alt=""/>
                    <span>铂涛旅行</span>
                    <i></i>
                </a>
                <a href="https://www.viator.com/content/account/SSL/signIn-form.jspa">
                    <img src={viator} alt=""/>
                    <span>Viator</span>
                    <i></i>
                </a>
            </div>
        );
    }
}


