import React from 'react';
import day from './../../images/day_icon.png';
const style={
    width:225,
    marginLeft:70,
    background:'url('+day+')no-repeat 205px center',
};
export default class Datepicker  extends React.Component{
    render (){
        return(
            <input type="text"  id={this.props.id} placeholder={this.props.placeholder} style={style}/>

        );
    };
}


