import React from 'react';
import mark from './../../images/landmark_icon.png';
export default class Textinput2 extends React.Component{
    render(){
        const style={
            input:{
                width:150,
                background:"url("+mark+") no-repeat 130px center",
                marginLeft:this.props.mleft
            }
        };
        return(<input type="text" style={style.input} placeholder={this.props.placeholder} id={this.props.id}/>);
    }
}


