//忘记密码
import React from "react";
import './../css/forget.css';
import {Link} from 'react-router-dom';
import logo from './../images/logo.png';
import Cookies from 'js-cookie';
import md5 from "js-md5";
export default class Forget extends React.Component{
    constructor(){
        super();
        this.state={
            data:0,
            errormsg:"",
            tell:"",
            repass:"",
            codes:""
        }
    }
    componentWillMount(){
        if(Cookies.get('name')){
            this.props.history.push('/login');
        }
    }
    componentDidMount(){this.setState({data:1});}
    componentDidUpdate(){
        const that=this;
        let {tell,repass,codes}=this.state;
        const count=document.getElementById('count');
        const code=document.getElementById('code');
        const codebtn=document.getElementById('codebtn');
        const pwd=document.getElementById('pwd');
        const repwd=document.getElementById('repwd');
        const forgetbtn=document.getElementById('forgetbtn');
        count.onblur=function(){
            const reg=/^1[34578]\d{9}/;
            if(this.value===""){
                that.setState({errormsg:"手机号不能为空"});
            }else{
                if(!reg.test(this.value)){
                    that.setState({errormsg:"请输入正确的手机号"});
                }else{
                    that.setState({tell:this.value,errormsg:""});
                }
            }
        };
        codebtn.onclick=function(){
            if(tell===""){
                that.setState({errormsg:"手机号不能为空"});
            }else{
                fetch("http://127.0.0.1:3001/api/auth/send_code?tel="+tell+"&type=2"+"&s="+md5(tell+"RJ5HXXCy5SH869n%4^aAu2p@Mk22sEF4"))
                    .then(res=>res.json())
                    .then(res=>{
                        if(parseInt(res.success)===0){
                            that.setState({errormsg:res.message});
                        }else{
                            console.log(res);
                            alert('短信验证码发送成功');
                        }});
            }
        };
        //验证码
        code.onblur=function(){
            if(this.value===""){
                that.setState({errormsg:"验证码不能为空"});
            }else{
                that.setState({codes:this.value});
            }
        };
        //密码
        pwd.onblur=function(){
            //必须是数字字母组合6-18位
            const reg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,18}$/;
            if(this.value===""){
                that.setState({errormsg:"密码不能为空"});
            }else{
                if(!reg.test(this.value)){
                    that.setState({errormsg:"密码设置错误"});
                }else{
                    that.setState({errormsg:""});
                }
            }
        };
        //重新输入密码
        repwd.onblur=function(){
            if(pwd.value!==this.value){
                that.setState({errormsg:"两次输入密码不一致"});
            }else{
                that.setState({errormsg:"",repass:this.value});
            }
        };
        //提交数据
        forgetbtn.onclick=function(){
            if(repass===""){return false}
            fetch("http://admin9.lohas-travel.com/api/auth/forget_password?tel="+tell+"&code="+codes+"&password="+repass)
                .then(res=>res.json()).then(res=>{
                if(parseInt(res.success)===0){
                    that.setState({errormsg:res.message});
                }else{
                    that.setState({errormsg:""});
                    alert('恭喜您，修改成功');
                    that.props.history.push('/login');
                }
            });
        };
    }

    render(){
        let {errormsg}=this.state;
        return(
            <div id="forget">
                {/*顶部开始*/}
                <div className="header">
                    <div className="w">
                        <Link to="/" className="logo">
                            <img src={logo} alt=""/>
                            <h1>乐活旅行-爱旅行 懂旅行</h1>
                        </Link>
                    </div>
                </div>
                {/*顶部结束*/}
                {/*内容开始*/}
                <div className="content">
                    <div className="w">
                        <div className="formwrapper">
                            <form>
                                <h1>找回密码</h1>
                                <div style={{height:20,color:"red"}}>{errormsg}</div>
                                <div className="backtologin"><Link to='/login'>返回登录</Link></div>
                                <h2>第一步 验证身份</h2>
                                <p>
                                    <input type="text" id="count" name="count" placeholder="请输入您注册时使用的手机号"/>
                                </p>
                                <p className="code">
                                    <input type="text" id="code" name="code" placeholder="请输入验证码(30分钟内有效)"/>
                                    <input type="button" id="codebtn" value="获取验证码"/>
                                </p>
                                <h2>第二步 修改密码</h2>
                                <p>
                                    <input type="password" id="pwd" name="pwd" placeholder="请输入6-18位登录密码"/>
                                </p>
                                <p>
                                    <input type="password" id="repwd" name="repwd" placeholder="请重新输入密码"/>
                                </p>
                                <p>
                                    <input type="button" value="确认修改" id="forgetbtn"/>
                                </p>
                            </form>
                        </div>
                    </div>
                </div>
                {/*内容结束*/}
                {/*底部开始*/}
                <div className="footer">Copyright © 2005-2018 版权所有 常州乐活旅游投资有限公司 苏ICP备15003740号</div>
                {/*底部结束*/}
            </div>
        );
    }
}


