//首页标题
import React from 'react';
const style={
    wrapper:{
        height:120,
        position:"relative"
    },
    middle:{
        width:400,
        margin:"0 auto",
        textAlign:"center",
        backgroundColor:"#fff"
    },
    big:{
        fontSize:36,
        marginTop:60
    },
    small:{
        fontSize:24,
        fontWeight:200,
        marginTop:15,
        marginBottom:50,
        color:"#666"
    },
    aline:{
        width:"100%",
        borderTop:"1px solid #e5e5e5",
        position:"absolute",
        top:80,
        zIndex:-1
    }
};
export default class Hometitle extends React.Component{
    render(){
        return(
            <div style={style.wrapper} className="w">
                <p style={style.aline}> </p>
                <div style={style.middle}>
                    <h1 style={style.big}>{this.props.big}</h1>
                    <p style={style.small}>{this.props.small}</p>
                </div>
            </div>
        );
    }
}


