import React from 'react';
import download_app from './../../images/download_app.png';
import qrcode from './../../images/qrcode.png';
export default class Download extends React.Component{
    render(){
        return(
            <div id="download">
                <h2>App下载</h2>
                <div>
                    <img src={download_app} alt="" style={{width:650,float:"left"}}/>
                    <img src={qrcode} alt="" style={{width:120,marginTop:150}}/>
                    <a href="https://itunes.apple.com/cn/app/le-huo-lu-xing/id1023257602?mt=8" className="download_btn" target="_blank">iphone客户端</a>
                    <a href="http://android.myapp.com/myapp/detail.htm?apkName=com.lohas.app" className="download_btn" target="_blank">Android客户端</a>
                </div>
            </div>
        );
    }
}


