//个人中心
import React from 'react';
import './../../css/individual.css';
export default class Individual extends React.Component{
    render(){
        return(
            <div id="individual">
                <h2>个人中心</h2>
                <div className="personalform">
                    <p>
                        <label htmlFor="phone">手机</label>
                        <input type="text" id="phone" placeholder="请输入手机号"/>
                    </p>
                    <p>
                        <label htmlFor="nickname">昵称</label>
                        <input type="text" id="nickname" placeholder="请输入昵称"/>
                    </p>
                    <p>
                        <label>性别</label>
                        <input type="radio" id="man" name="sex" value="man" checked/>男
                        &nbsp;&nbsp;&nbsp;&nbsp;
                        <input type="radio" id="woman" name="sex" value="woman"/>女
                    </p>
                    <p>
                        <label htmlFor="signature">签名</label>
                        <input type="text" id="signature" placeholder="请输入个性签名"/>
                    </p>
                    <p>
                        <input type="button" id="individualbtn" value="保存"/>
                    </p>
                </div>
            </div>
        );
    }
}


