//app入口
import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter as Router,Route} from 'react-router-dom';
import registerServiceWorker from './registerServiceWorker';
import Home from './js/home';
import Hotel from './js/hotel';
import Login from './js/login';
import Regist from './js/regist';
import Forget from './js/forget';
import Company from './js/company';
import Personal from './js/personal';
import Map from './js/map';
import './css/style.css';
const App=()=>(
    <Router>
        <div>
            <Route exact path='/' component={Home}/>
            <Route path='/hotel/:val' component={Hotel}/>
            <Route path='/login' component={Login}/>
            <Route path='/forget' component={Forget}/>
            <Route path='/regist' component={Regist}/>
            <Route path='/company' component={Company}/>
            <Route path='/personal' component={Personal}/>
            <Route path='/map/:val' component={Map}/>
        </div>
    </Router>
);
ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



