import React from 'react';
import recruit from './../../images/recruit.jpg';
export default class Recruit extends React.Component{
    render(){
        return(
            <div id="recruit">
                <img src={recruit} alt=""/>
                <h2>工作在乐活</h2>
                <p>我们拥有一个充满活力的团队。在这里，你既有机会从团队的同事身上汲取经验，也可以从互联网获取更多的宝贵经验，提高自身的工作能力</p>
                <p>我们是一个由活跃的年轻人组成的团队，有很强的团队精神和责任心。同时也鼓励和帮助员工通过不同的培训提高自己，并和公司共同成长。我们对工作有很高的要求，并为共同的目标而努力，但是我们同样在闲暇时间尽情享受生活。</p>
                <p>我们有人性化的工作环境，良好的福利计划，有竞争力的薪资待遇，定期的学习和交流机会。当然更重要的是，在乐活旅行工作将会是你职业发展道路上一次宝贵和愉快的经验。</p>
                <h2>我们提供:</h2>
                <p>有竞争力的薪酬待遇</p>
                <p>轻松的工作氛围</p>
                <p>五险一金，让你后顾无忧</p>
                <p>优厚的旅游福利</p>
                <p>免费的在职培训</p>
                <p>免费午餐,外加各种聚会</p>
                <p><strong>希望加入我们？</strong></p>
                <p>请将您的简历发送至: <span style={{color:"deepskyblue"}}>hr@lohas-travel.com</span>，并注明应聘职位。</p>
                <p>工作地点：江苏常州</p>
            </div>
        );
    }
}


