//个人中心
import React from 'react';
import {Link,Route} from 'react-router-dom';
import Header from './component/header';
import Footer from './component/footer';
import Individual from './component/individual';
import Orders from './component/orders';
import Cookies from 'js-cookie';
import './../css/personal.css'
export default class Personal extends React.Component{
    constructor(){
        super();
        this.state={
            name:Cookies.get("name")
        }
    }
    componentWillMount(){
        if(Cookies.get("name")===undefined){
           this.props.history.push('/login');
        }
    }
    componentDidMount(){
        const oLis=document.getElementById('personallists').getElementsByTagName('a');
        for(let i=0; i<oLis.length; i++){
            oLis[i].onclick=function(){
                for(let i=0; i<oLis.length; i++){
                    oLis[i].className="";
                }
                this.className="active";
            }
        }
    }
    render(){
        return(
            <div id="personal">
                {/*头部开始*/}
                <Header val={this} path='/login'/>
                {/*头部结束*/}
                {/*个人中心开始*/}
                <div className="w" id="personalwrapper">
                    {/*左边列表开始*/}
                    <div className="menu">
                        <div className="menutitle">我的乐活</div>
                        <div className="lists" id="personallists">
                            <Link className="active" to="/personal">个人中心<i></i></Link>
                            <Link to="/personal/orders">我的订单<i></i></Link>
                        </div>
                    </div>
                    {/*左边列表结束*/}
                    {/*右边内容开始*/}
                    <div className="content">
                        <Route exact path="/personal" component={Individual}/>
                        <Route path="/personal/orders" component={Orders}/>
                    </div>
                    {/*右边内容结束*/}
                </div>
                {/*个人中心结束*/}
                {/*底部开始*/}
                <Footer/>
                {/*底部结束*/}
            </div>
        );
    }
}


