import React from 'react';
import aboutimg from './../../images/about.png';
export default class Introduce extends React.Component{
    render(){
        return(
            <div id="introduce">
                <img src={aboutimg} alt="常州乐活旅行投资有限公司"/>
                <h2>关于乐活旅行</h2>
                <p>“乐活旅行网”－是常州乐活旅游投资有限公司开发的专注服务于自助旅游客户的搜索比价预订网站。</p>
                <p >“乐活旅行App”－是常州乐活旅游投资有限公司开发的专注服务于自助旅游客户的搜索比价预订的App。</p>
                <h2>我们提供什么？</h2>
                <p>我们开发运营的“乐活旅行网和App”，与国内外知名旅游运行商合作，如：携程 同程 艺龙 驴妈妈 booking agoda 天巡 Viator Expedia等企业合作，解决用户的机票 酒店 餐厅的搜索比价和预定。让用户在最短的时间内找到同种类型服务的最低价格。</p>
                <p>-同时给予用户在旅行前对目的地的选择提供支持，我们评选出各类旅游”Top10”板块，精选出各类目的地资讯，通过我们的手机App可以很好的了解目的地的“吃 住 行 玩 购”各个方面的信息，了解位置，预定，图片，人气，消费价格，评论等一系列资讯，便于用户更好的规划旅行行程。</p>
                <p>- 在旅途中，您可以配合我们的“乐活旅行App”, 使用“附近”功能，及时的了解周边的旅游信息，离目的地的远近，导航，人气等，真正成为“乐活旅行－您的随身即时旅游向导”。</p>
                <p>我们努力排除掉多余的资讯，减少无用信息的干扰，使用户更方便的做出选择。</p>
                <p>我们提出的旅行行程规划思路是：寻找目的地－选择交通－筛选酒店－发现游玩－寻觅美食－体验购物。</p>
                <p> “乐活旅行”提供所有的免费旅游资讯，不收取用户任何费用。</p>
                <p>我们的目标是要覆盖全球所有的旅行目的地资讯，帮助旅行者更好的旅行。</p>
                <h2>为什么选择我们？</h2>
                <p> “乐活旅行网”专注于自由行客户，“一切从旅行者的角度思考”是我们工作的指导思想，但我们更注重的是用户体验。我们用专业帮助用户快速的寻找到需要且有用的资讯，简化选择的便利性，释放出用户更多的时间去享受旅行中的乐趣，而不是花费大量的时间去寻找和搜集信息。        </p>
                <p>“乐活旅行”想伴随所有的旅行者共同成长！</p>
            </div>
        );
    }
}


