//页面底部区域
import React from 'react';
import './../../css/footer.css';
import {Link} from 'react-router-dom';
import free from './../../images/1_free.png';
import team from './../../images/2_team.png';
import africa from './../../images/3_africa.png';
import app from './../../images/4_app.png';
export default class Footer extends React.Component{
    render(){
        return(
            <div id="footer">
                <div className="w">
                    <div className="lists">
                        <div className="item webname">乐活旅行</div>
                        <div className="item">
                            <div className="listheader">关于我们</div>
                            <div className="listbody">
                                <Link to="/company">乐活介绍</Link>
                                <Link to="/company/cooprator">合作伙伴</Link>
                                <Link to="/company/recruit">招聘信息</Link>
                                <Link to="/company/connect">联系我们</Link>
                            </div>
                        </div>
                        <div className="item">
                            <div className="listheader">消费者帮助</div>
                            <div className="listbody">
                                <Link to="/company/download">APP下载</Link>
                                <Link to="/company/law">法律法规</Link>
                                <Link to="/company/payment">支付信息</Link>
                            </div>
                        </div>
                        <div className="item">
                            <div className="listheader">产品中心</div>
                            <div className="listbody products">
                                <p>
                                    <a href="http://www.lohas-travel.com/" target="_blank" rel="noopener noreferrer"><img src={app} alt="旅游APP"/></a>
                                    <a href="http://www.wildafrica.cn/" target="_blank" rel="noopener noreferrer"><img src={africa} alt="非洲旅游"/></a>
                                </p>
                                <p>
                                    <Link to="/"><img src={free} alt="自由行"/></Link>
                                    <a href="http://wxskb.lvyouquan.cn/changzhoulehuo/Product/NewIndex" target="_blank" rel="noopener noreferrer"><img src={team} alt="跟团旅游"/></a>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="copyright">Copyright©常州乐活旅游投资有限公司，All Right Reserved</div>
                </div>
            </div>
        );
    }
}


