import React from 'react';
import {Link} from 'react-router-dom';
import Cookies from 'js-cookie';
import './../css/login.css';
import logo from './../images/logo.png';
export default class Login extends React.Component{
    constructor(){
        super();
        this.state={
            data:"",
            errormsg:""
        }
    }
    componentWillMount(){
        if(Cookies.get('name')){
            this.props.history.push('/personal');
        }
    }
    componentDidMount(){
        this.setState({data:1});
    }
    componentDidUpdate(){
        const that=this;
        const count=document.getElementById('count');
        const pwd=document.getElementById('pwd');
        const loginbtn=document.getElementById('loginbtn');
        loginbtn.onclick=function(){
           let countval=count.value;
           let pwdval=pwd.value;
           fetch("http://admin9.lohas-travel.com//api/auth/user_signin?tel="+countval+"&password="+pwdval)
               .then(res=>res.json()).then(res=>{
                   if(res.success===0){
                       that.setState({errormsg:res.message});
                   }else{
                       Cookies.set('name',countval,{expires:1});
                       that.props.history.push('/personal');
                   }
           });
        }
    }
    render(){
        return(
            <div id="login">
                {/*顶部开始*/}
                <div className="header">
                    <div className="w">
                        <Link to="/" className="logo">
                            <img src={logo} alt=""/>
                            <h1>乐活旅行-爱旅行 懂旅行</h1>
                        </Link>
                    </div>
                </div>
                {/*顶部结束*/}
                {/*内容开始*/}
                <div className="content">
                    <div className="w">
                        <div className="formwrapper">
                            <form>
                                <h1>用户登录</h1>
                                <div style={{color:"red",height:25}}>{this.state.errormsg}</div>
                                <p>
                                    <label htmlFor="count" className="countlabel"> </label>
                                    <input type="text" id="count" name="count" placeholder="请输入账号" maxLength="11"/>
                                </p>
                                <p>
                                    <label htmlFor="pwd" className="pwdlabel"> </label>
                                    <input type="password" id="pwd" name="pwd" placeholder="请输入密码" maxLength="18"/>
                                </p>
                                <p>
                                    <input type="button" defaultValue="登录" id="loginbtn"/>
                                </p>
                                <p>
                                    <Link to="/forget" style={{float:"right"}}>忘记密码</Link>
                                    <Link to="/regist">立即注册</Link>,享受乐活会员优惠!
                                </p>
                            </form>
                        </div>
                    </div>
                </div>
                {/*内容结束*/}
                {/*底部开始*/}
                <div className="footer">Copyright © 2005-2018 版权所有 常州乐活旅游投资有限公司 苏ICP备15003740号</div>
                {/*底部结束*/}
            </div>
        );
    }
}


