import React from 'react';
import Header from './component/header';
import Footer from './component/footer';
import {Link} from 'react-router-dom';

import './../css/map.css';

export default class Map extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            data:"",
            scope:5,
            name:this.props.match.params.val,
            point:""
        }
    }
    componentDidMount(){

            //获得浏览器可视高度，并做IE兼容
            const wH=document.documentElement.clientHeight||document.body.clientHeight;
            //设置地图默认高度
            document.getElementById('mapwrapper').style.height=wH-85+'px';
            //获取地图初始化数据
            let BMap=window.BMap;//获取百度地图SDK
            let mp = new BMap.Map("mymap");//获取百度地图
            let localSearch = new BMap.LocalSearch(mp);//获取根据地名搜索的方法

            localSearch.search(this.props.match.params.val);
            localSearch.setSearchCompleteCallback(searchResult=>{//获取坐标和酒店列表初次加载的数据
                let poi = searchResult.getPoi(0);
                fetch('http://admin9.lohas-travel.com/api/hotel/hotel_list_map?lat='+poi.point.lat+'&lng='+poi.point.lng+'&scope=5')
                    .then(res=>res.json()).then(res=>this.setState({point:poi.point,data:res.data}));
            });
    }
    getData(){
        const {scope}=this.state.scope;
        //获取地图初始化数据
        let BMap=window.BMap;//获取百度地图SDK
        let mp = new BMap.Map("mymap");//获取百度地图
        let localSearch = new BMap.LocalSearch(mp);//获取根据地名搜索的方法
        localSearch.search(this.state.name);
        localSearch.setSearchCompleteCallback(searchResult=>{//获取坐标和酒店列表初次加载的数据
            let poi = searchResult.getPoi(0);
            fetch('http://admin9.lohas-travel.com/api/hotel/hotel_list_map?lat='+poi.point.lat+'&lng='+poi.point.lng+'&scope='+scope)
                .then(res=>res.json()).then(res=>this.setState({point:poi.point,data:res.data}));
        });
    }
    componentDidUpdate(){
        let {point,data,name}=this.state;
        const that=this;
        // 百度地图API功能 必须写在这个方法里负责无法通过改变state值而动态请求数据
        let BMap=window.BMap;
        let mp = new BMap.Map("mymap",{enableMapClick:false,minZoom:4});
        mp.enableDoubleClickZoom();
        mp.enableInertialDragging();
        mp.enableScrollWheelZoom();
        mp.centerAndZoom(point,14);//执行两次原因是在此函数外部用坐标定位

        // 复杂的自定义覆盖物
        function ComplexCustomOverlay(point,item){
            this.point = point;
            this.star=item.fake_comment_score;
            this.price=item.low_price;
            this.Bcolor=item.comment_color;
            //定义视窗口
            this.inforHtml=`<div class="listitems">
                            <div class="pic" style="background-image:url(${item.image})"></div>
                            <div class="info">
                                <h1 class="title">${item.title}</h1>
                                <p class="category">${item.category_list.map((item,index)=>"<span>"+item.title+"</span>")}</p>
                                <p class="address">${item.position!==null?"距离"+item.position.title+"<span>"+Number(item.position.pos_distance).toFixed(2)+"公里</span>":""}</p>
                                <div class="details">
                                    <div class="left">
                                        <p class="comments">
                                            <span class="stars" style="background-color:${item.comment_color}">${item.comment} ${item.fake_comment_score}</span>
                                            <span class="counts">(${item.comment_num}条点评)</span>
                                        </p>
                                    </div>
                                    <div class="right">
                                        <span><s>￥${item.sale!==null?item.sale.max:''}</s>&nbsp;&nbsp;&nbsp;<i>￥${item.sale!==null?item.sale.min:""}</i></span>
                                        <a href="#/hotelselectdetail/${item.id}">查看详情</a>
                                    </div>
                                </div>
                            </div>
                        </div>`;
        }
        ComplexCustomOverlay.prototype = new BMap.Overlay();
        //定义复杂覆盖物
        ComplexCustomOverlay.prototype.initialize = function(map){
            this._map = map;
            const that=this;
            let div = this._div = document.createElement("div");
            div.className="bmapmark";
            div.style.zIndex = BMap.Overlay.getZIndex(this.point.lat);
            if(this.star===""||Number(this.star)===0){
                div.innerHTML=this.price+"<p class='bmaparrow emp'> </p>";
                div.style.paddingLeft="5px";
            }else{
                div.innerHTML="<div class='bmapstar' style='background-color:"+this.Bcolor+"'>"+this.star+"</div>"+this.price+"<p class='bmaparrow'> </p>";
            }
            const mapInfowindow=document.getElementById('mapInfowindow');
            div.onclick=function(){
                const oDiv=this.parentElement.getElementsByClassName('bmapmark');
                for(let i=0; i<oDiv.length; i++){
                    oDiv[i].className="bmapmark";
                }
                this.className="bmapmark active";
                mapInfowindow.innerHTML=that.inforHtml;
                mapInfowindow.style.display="block";
            };
            mp.ondragend=()=>{mapInfowindow.style.display="none";};//拖拽地图结束，隐藏
            mp.getPanes().labelPane.appendChild(div);
            return div;
        };
        ComplexCustomOverlay.prototype.draw = function(){
            let map = this._map;
            let pixel = map.pointToOverlayPixel(this.point);
            this._div.style.left = pixel.x+"px";
            this._div.style.top  = pixel.y-55 + "px";
        };
        if(data.length>=500){
            data=data.slice(0,500);
        }
        data?data.map(item=>{
                let myCompOverlay = new ComplexCustomOverlay(new BMap.Point(item.lng,item.lat),item);
                mp.addOverlay(myCompOverlay);
        }):"";

        // 搜索和距离选择
        // 根据id获取元素闭包
        function getID(val){return document.getElementById(val);}
        const citysearch=getID('citysearch');
        citysearch.value=name;
        const searchbtn=getID('searchbtn');
        searchbtn.onclick=()=>{
            if(citysearch.value===""){
                alert('请输入城市');
            }else{
                this.setState({name:citysearch.value});
                this.getData();
            }
        };
        const hoteldistances=getID('hoteldistances');
        const distanceitem=hoteldistances.getElementsByTagName('i');
        for(let i=0; i<distanceitem.length; i++){
            distanceitem[i].onclick=function(){
                for(let i=0; i<distanceitem.length; i++){
                    distanceitem[i].className="";
                }
                this.className="active";
                that.setState({scope:this.getAttribute('data-type')});
                that.getData();
            }
        }
    }
    render(){
        let {name,data,scope}=this.state;
        return(
            <div id="map" style={{minWidth:1200}}>
                <Header/>
                <div style={{height:"500px",position:"relative"}} id="mapwrapper">
                    <div id="mapHotelcount">附近{scope}公里，为您推荐：{data.length}家酒店</div>
                    <div id="mapInfowindow"> </div>
                    <div id="mymap" style={{width:'100%',height:"100%"}}> </div>
                    <div id="mapFilter">
                        <p>
                            <input type="text" id="citysearch" placeholder="请输入城市" maxLength="20"/>
                            <input type="button" id="searchbtn" value="搜索"/>
                        </p>
                        <p>
                            <span style={{color:'#666'}}>距离(km):</span>
                            <span className='distances' id="hoteldistances">
                                <i data-type="1">1</i>
                                <i className="active"  data-type="5">5</i>
                                <i data-type="10">10</i>
                                <i data-type="20">20</i>
                            </span>
                        </p>
                        <p style={{height:35,lineHeight:"35px"}}>
                            <Link to={'/hotel/'+name} id="tohotellist">返回酒店列表</Link>
                        </p>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}


