import React from 'react';
import mark from './../../images/landmark_icon.png'
export default class Textinput extends React.Component{
    render(){
        return(
            <input type="text"  id={this.props.id} placeholder={this.props.placeholder} style={{background:'url('+mark+') no-repeat right center',width:this.props.wth}}/>
        );
    }
}


