import React from 'react';
const style={
    width:170,
    textAlign:"center",
    color:"#fff",
    backgroundColor:"#ff9f00",
    float:"right",
    cursor:"pointer"
    };
export default class Searchbtn extends React.Component{
    render(){
        return(
            <div className="searchbtn" id={this.props.id} style={style}>搜索</div>
        );
    }
}


