import React from 'react';
import './../../css/hotelmap.css';
export default class Hotelmap extends React.Component{
    componentDidMount(){
        let BMap=window.BMap;
        let that=this;
        // 百度地图API功能
        let mp = new BMap.Map(this.props.id);
        mp.centerAndZoom(new BMap.Point(this.props.lng,this.props.lat),15);
        mp.enableScrollWheelZoom();
        mp.enableDoubleClickZoom();
        mp.enableInertialDragging();
        // 复杂的自定义覆盖物
        function ComplexCustomOverlay(point){
            this.point = point;
            this.star=that.props.star;
            this.price=that.props.price;
        }
        ComplexCustomOverlay.prototype = new BMap.Overlay();
        ComplexCustomOverlay.prototype.initialize = function(map){
            this._map = map;
            let div = this._div = document.createElement("div");
            div.className="bmapmark";
            div.style.zIndex = BMap.Overlay.getZIndex(this.point.lat);
            if(this.star===""){
                div.innerHTML="￥"+this.price+"<p class='bmaparrow'> </p>";
                div.style.paddingLeft="8px";
            }else{
                div.innerHTML="<div class='bmapstar'>"+this.star+"</div>"+this.price+"<p class='bmaparrow'> </p>";
            }
            mp.getPanes().labelPane.appendChild(div);
            return div;
        };
        ComplexCustomOverlay.prototype.draw = function(){
            let map = this._map;
            let pixel =map.pointToOverlayPixel(this.point);
            this._div.style.left = pixel.x + "px";
            this._div.style.top  = pixel.y + "px";
        };
        let myCompOverlay = new ComplexCustomOverlay(new BMap.Point(that.props.lng,that.props.lat));
        mp.addOverlay(myCompOverlay);
    }
    render(){
        return(
            <div className="hotelmap" id={this.props.id} style={{width:760,height:500}}> </div>
        );
    }
}


