import React from 'react';
export default class Law extends React.Component{
    render(){
        return(
            <div id="law">
                <h1 style={{textAlign:"center",marginBottom:"15px"}}>保密政策</h1>
                <p>
                    <a href="http://www.lh-lx.com/" target="_blank" style={{color:"deepskyblue"}}>www.lh-lx.com</a>(乐活旅行网)是一项推荐旅游目的地、酒店、景点、活动、美食、购物等旅行指南以及其他内容的网络服务网站，由常州乐活旅行投资有限公司运营。
                </p>
                <p>
                    我们理解，您通过网络向我们提供信息是建立在您对我们信任的基础上。我们非常认真地对待您的信任，并且将确保将您提供给我们的个人信息的安全性和保密性作为重点。在 您将个人信息提供给我们网站之前，请您仔细阅读本保密政策以 了解我们的保密措施。在访问本网站www.lh-lx.com的同时，您确认了解并接受在此介绍的保密措施。
                </p>
                <p><strong>我们从您处收集哪些信息 </strong></p>
                <h2>概述 </h2>
                <p>我们将接收并且储存您在我们网站上键入或者以任何其他方式提供给我们的所有信息，包括能够识别您身份的信息（“个人信息”），包括您的姓名、邮寄 地址以及电子邮 箱地址。您可以选择不向我们提供信息，但是，概括而言，为了您能够以注册会员的身份享用我们提供的便利，你需要提供某些信息。 例如，只有注册会员能够发表旅游评论，参与论 坛讨论，将乐活旅行的内容以电子邮件的形式发送给自己以及他人，获取会员专享的通讯以及有关旅行 计划的内容，参与调查、竞赛或者赢奖。为了您能够在我们网站上向我们提问或者 开展其他交易，您也需要提供某些信息。</p>
                <h2>自动收集的信息 </h2>
                <p>当您访问乐活旅行时，我们将自动收集有关您电脑的信息。例如，我们将会获取您的IP地址，网页浏览器软件类型（例如Netscape Navigator 或者Internet Explorer），以及正在 浏览的网站等。我们也可能会收集有关您上网活动的信息，例如浏览的内容和访问的网页等。我们自动收集上述信息的目的之一是 帮助我们理解您的兴趣所在并且为您提供个性化服务 。</p>
                <h2>Cookies 文档以及其他网页技术</h2>
                <p>当您访问乐活旅行时，我们将会向您的电脑分配一个cookie文本文件（一个小型的、唯一识别的文本文件）。此外，如果您在我们的网站上注册，我们将可能储存您电脑 cookie文件 夹中的所有或者部分信息。如果您选择“保存该密码以自动登陆”这一选项，那么我们会把您的密码储存在cookie文件中。这将使我们在您访问我们网站时能 够保存您的偏好，并且向您 呈现一个个性化的网站，以免您每次访问时都需重新登陆。请您注意，出于安全性的考虑，所有储存在cookies文件中的会员身份信息、密码 以及其他与会员账号有关的信息都经过加 密处理。我们也可能使用网页信标（也称作clear gifs, pixel tags 或者Web bugs）来发送cookies文件或者与之进行信息互通 ，计算访问我们网站某些网页的用户人数，并且了解用 户是否经由第三方网站的网络广告而访问我们网站。</p>
                <h2>第三方收集的信息</h2>
                <p>第三方，例如供应商、广告商以及合作伙伴，可能会使用cookies文件以及其他技术（例如网页信标）以收集有关您上网活动的信息。收集这些信息的目的包括：掌握 我们 网站的使用情况；分析、修改在www.lohas-travel.com以及其他网站上展示的广告或者使之个性化；为您提供您可能感兴趣的商 品或者服务的广告。我们对于第三方使用 的cookies 文件以及其它功能无权查看，也不能控制。此外，本保密政策并不包括第三方的保密措施。</p>
                <p><strong>我们如何使用您的信息</strong></p>
                <p>一般而言，乐活旅行将把收集到的有关您的信息用于以下目的：为您提供您所希望获得的产品和服务；管理您的帐户，包括便于您登陆以及使用我们网站；与 您进行交流 ，包括提供有关道道网的信息以及来自乐活旅行社区的邀请；回答您的提问与意见；考量我们的产品、服务与网站是否有吸引力并加以提高；让您 了解我们或者其他第三方能提供的您可 能感兴趣的特殊产品和服务；使您使用乐活旅行的体验个性化; 以调查或其他方式从您处获取信息；防止潜在的违法 或遭禁止的行为；实施我们的使用条款；以及在向您收集信息时向您 指明的其它目的。</p>
                <p>请阅读以下“有关您信息的获取以及使用，您有哪些选择”一栏</p>
                <h2>我们与谁共享您的信息</h2>
                <p>乐活旅行 可能与以下机构共享您的信息:</p>
                <h2>第三方供应商</h2>
                <p>第三方供应商代表我们向您提供服务，包括商业分析、客户服务、市场推广、开展调查或者赢奖活动，以及预防欺诈。我们也可能会授权第三方供应商代表我们向 您收集信息，包 括为实现我们网站的功能所必需的信息或者为发布符合您个人兴趣的网络广告所需的信息。只有在第三方供应商为履行其工作所需时，其才有权 了解或者收集您的信息，并且他们不得 为任何其他用途透露或者使用您的信息。</p>
                <h2>合作伙伴</h2>
                <p>我们可能与合作伙伴一起为您提供产品或者服务。我们的合作伙伴都是全球知名的服务供应商。当有第三方参与提供您所希望获得的产品或服务时，您能轻易发现这一点，因为他们的名字将会与我们的名字 一同显示。如果您 愿意(您有权自主决定是否愿意)接受该服务，我们将与相关合作伙伴共享您的信息，包括个人信息。请您注意，我们并不控制第三方合作伙伴的保密措施。</p>
                <h2>我们也可能会在以下情况下与他人共享您的信息: </h2>
                <p>为了回应传票、法院命令或者其他法律程序；为了使我们的法律权利得以生效或实施；为了就诉讼进行抗辩；或者按照法律的规定披露。在上述情况下，我们保留 行使或者放弃反 对权和我们依法能够行使的任何其他权利的权利。
                    当我们认为调查、防止或者采取措施以处理违法或者涉嫌违法的行为是适当的；为了保护及维护乐活旅行、我们的客户或他人的权利、财产及安全；以及有关于我们的服务条 款和其他协议的情况。</p>
                <p>有关于公司交易的情形，例如股份强制转让、合并、兼并，或者资产出售，或未必发生的破产。 </p>
                <p>如果您选择在乐活旅行建立档案，公众将会通过您的屏幕名称查看及获悉您档案中的部分信息。与公众一样，您也可以查看您的档案并且可以随时编辑您的档案，以删除或修 改您 选择公开的信息。此外，如果您选择参与我们的会员间论坛，其他乐活旅行会员可能以到到网作为中介与您取得联系；但是乐活旅行不会将您的电子邮件地址透露给其 他会员，也不会加以 公开。所有乐活旅行会员有权在任何时间终止会员间论坛的功能。</p>
                <p>除了上述情形，当我们与第三方共享您的个人信息时，我们将会通知您，并且您有权拒绝该信息共享。</p>
                <p>我们也可能与包括广告商及投资者在内的第三方共享总计或者匿名信息。例如，我们可能告知广告商我们网站的访问者人数，或者最受欢迎的酒店或者旅游地。这些信息不含 有 任何个人信息，我们将其用于开发您可能感兴趣的项目和服务。</p>
                <h2>您如何查看您的信息</h2>
                <p>我们将采取合理措施以确保您的信息准确、完整并且按照预定用途使用。您可以通过访问到到网网站上的“会员档案”网页来查看以及更新您的联系信息。如果您 希望关闭会员账 户，请访问我们的常见问题页面。 请您注意：当您关闭帐户后，您将无法登陆或者查看您的个人信息。但是，您可以在任何时间开立一个新帐 户。此外，我们也提醒您注意，我们可能会把与您帐户相关的信息保留在我们的档案中，用作分析、存档以及其他目的。</p>
                <h2>有关您信息的获取以及使用，您有哪些选择</h2>
                <p>如前述，您可以选择不向我们提供任何信息，但是这样一来您将无法使用乐活旅行的部分功能。</p>
                <p>如前述，您可以添加、更新您的信息或者关闭您的帐户。</p>
                <p>当您注册成为乐活旅行的会员后，您可以选择是否接受乐活旅行发送的电子邮件，包括有关特殊产品或服务的通讯和电子公告。 作为一名乐活旅行的注册会员，您可以在任 何时间通过“ 会员档案”网页更改您的选择。无论您是否注册成为乐活旅行的会员，您都可以选择不再接受我们通过电子邮件发送的广告信息。请您注意，我们保留无需您同 意而发送给您其他信息的 权利，这些信息包括服务说明、管理信息以及调查信息等。</p>
                <h2>我们如何保护您的信息</h2>
                <p>我们希望您使用乐活旅行时能感到放心，并且我们致力于保护我们收集的信息。尽管没有一家网站能够保证完全安全，我们已经采取了合适的管理、技术以及有 形措施以保护 您提供给我们的个人信息。例如，只有经授权的员工能够查看个人信息，并且只能用于经许可的工作目的。此外，在敏感的个人信息传输于我们的 系统与您的系统之间时，我们将采取 加密措施。我们也设置了防火墙以及入侵侦查系统以防止未经授权的人员获取您的信息。</p>
                <h2>儿童的保密</h2>
                <p>乐活旅行是面向大众的网站，并非提供专门面向儿童的服务。如果我们了解到一名13岁以下儿童将其个人信息发送给我们，对于该信息，我们将仅用于回复该儿 童以告知我们 在接受其信息之前必须获得其父母的同意。 </p>
                <h2>外部链接</h2>
                <p>如果乐活旅行网站上的任何部分将您链接至其他网站，这些网站并不受本保密政策的限制。我们建议您仔细阅读在这些网站上公布的保密申明以了解他们收集、 使用、披露个 人信息的程序。</p>
                <h2>从中国大陆以外地区访问我们网站</h2>
                <p>如果您从中国大陆以外地区访问我们网站，请您注意：您的信息可能被传送至、储存于中国大陆，并且在中国大陆进行处理，这是因为我们的服务器以及中央数 据库均位于中国大 陆。中国及其他国家的数据保护以及其他法律可能未必如你所在国家相关法律那样健全，但是请相信我们将采取措施保护您的信息。在使用 我们服务的同时，您认识到您的信息可能被 传送至我们的机构，并且如本保密政策所述，我们可能与第三方共享您的信息。</p>
                <h2>本保密政策的修改</h2>
                <p>乐活旅行可能在将来更新本保密政策。我们将会通过向您提供给我们的电子邮箱发送通知或者在我们网站上发布显著公告的方式将主要的修改内容告知于您。</p>
                <h2>您如何与我们联系</h2>
                <p>有关详细信息，请访问我们的常见问题页面。</p>
                <p></p>
                <p>如您有关于本保密政策的任何问题，请联系：</p>
                <p>常州乐活旅游投资有限公司</p>
                <p>江苏常州科教城创研港1号楼15层</p>
                <p>邮 编：213000</p>
                <p>电 话：0519-81163528</p>
                <p></p>
                <p style={{color:"red"}}>本保密政策于2015.01.01生效。</p>

            </div>
        );
    }
}


