//注册页面
import React from 'react';
import {Link} from 'react-router-dom';
import './../css/regist.css';
import logo from './../images/logo.png';
import md5 from 'js-md5';
import Cookies from 'js-cookie';
export default class Regist extends React.Component{
    constructor(){
        super();
        this.state={
            data:"",
            errormsg:"",
            tell:"",
            codes:"",
            repass:"",
            nick:"",
            sex:"男"
        }
    }
    componentWillMount(){
        if(Cookies.get('name')){//当用户登录直接跳到个人中心
            this.props.history.push('/personal');
        }
    }
    componentDidMount(){
        this.setState({data:1});
    }
    componentDidUpdate(){
        let {tell,codes,repass,nick,sex}=this.state;
        const that=this;
        const count=document.getElementById('count');
        const code=document.getElementById('code');
        const codebtn=document.getElementById('codebtn');
        const pwd=document.getElementById('pwd');
        const repwd=document.getElementById('repwd');
        const nickname=document.getElementById('nickname');
        const man=document.getElementsByName('sex');
        const woman=document.getElementById('woman');
        const proto=document.getElementById('proto');
        const registbtn=document.getElementById('registbtn');
        //检测用户输入的手机号是否合法

        count.onblur=function(){
            const reg=/^1[34578]\d{9}$/;
            if(this.value===""){
                that.setState({
                    errormsg:"手机号不能为空"
                });
            }else{
                if(!reg.test(this.value)){
                    that.setState({
                        errormsg:"请输入正确的手机号"
                    });
                }else{
                    that.setState({errormsg:"", tell:this.value});
                }
            }
        };
        //获取短信验证码
        codebtn.onclick=function(){
            if(tell===""){
                that.setState({errormsg:"手机号不能为空"});
            }else{
                fetch("http://admin9.lohas-travel.com/api/auth/send_code?tel="+tell+"&type=2"+"&s="+md5(tell+"RJ5HXXCy5SH869n%4^aAu2p@Mk22sEF4"))
                    .then(res=>res.json())
                    .then(res=>{
                        if(parseInt(res.success)===0){
                            that.setState({errormsg:res.message});
                        }else{
                            console.log(res);
                            alert('短信验证码发送成功');
                        }});
            }
        };
        //验证码
        code.onblur=function(){
            if(this.value===""){
                that.setState({errormsg:"验证码不能为空"});
            }else{
                that.setState({codes:this.value});
            }
        };
        //密码
        pwd.onblur=function(){
            //必须是数字字母组合6-18位
            const reg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,18}$/;
            if(this.value===""){
                that.setState({errormsg:"密码不能为空"});
            }else{
                if(!reg.test(this.value)){
                    that.setState({errormsg:"密码设置错误"});
                }else{
                    that.setState({errormsg:""});
                }
            }
        };
        //重新设置密码
        repwd.onblur=function(){
            if(pwd.value!==this.value){
                that.setState({errormsg:"两次输入密码不一致"});
            }else{
                that.setState({errormsg:"",repass:this.value});
            }
        };
        //昵称
        nickname.onblur=function(){
            if(this.value===""){
                that.setState({errormsg:"昵称不能为空"});
            }else{
                that.setState({errormsg:"",nick:this.value});
            }
        };
        //男
        man.onclick=function(){
            that.setState({sex:"男"});
        };
        woman.onclick=function(){
            that.setState({sex:"女"});
        };
        //如果乐活协议没有选中
        if(!proto.checked){
            proto.onclick=function(){
                this.checked=true;
                that.setState({errormsg:""});
            };

        }else{
            proto.onclick=function(){
                this.checked=false;
                that.setState({errormsg:"请阅读和选择服务条款"});
            };
        }
        registbtn.onclick=function(){
            if(!proto.checked){
                that.setState({errormsg:"请阅读和选择服务条款"});
            }else{
                fetch("http://admin9.lohas-travel.com/api/auth/user_signup?tel="+tell+"&nick="+nick+"&code="+codes+"&password="+repass+"&sex="+sex)
                    .then(res=>res.json()).then(res=>{
                        if(parseInt(res.success)===0){
                            that.setState({errormsg:res.message});
                        }else{
                            that.setState({errormsg:""});
                            alert('恭喜您，注册成功');
                            that.props.history.push('/login');
                        }
                });
            }
        }
    }
    render(){
        let errormsg=this.state.errormsg;
        return(
            <div id="regist">
                {/*顶部开始*/}
                <div className="header">
                    <div className="w">
                        <Link to="/" className="logo">
                            <img src={logo} alt=""/>
                            <h1>乐活旅行-爱旅行 懂旅行</h1>
                        </Link>
                    </div>
                </div>
                {/*顶部结束*/}
                {/*内容开始*/}
                <div className="content">
                    <div className="w">
                        <div className="formwrapper">
                            <form>
                                <h1>用户注册</h1>
                                <div style={{height:25,color:"red"}}>{errormsg}</div>
                                <div className="backtologin"><Link to='/login'>返回登录</Link></div>
                                <p>
                                    <input type="text" id="count" name="count" placeholder="请输入手机号" maxLength="11"/>
                                </p>
                                <p className="code">
                                    <input type="text" id="code" name="code" placeholder="请输入验证码(30分钟有效)" maxLength="6"/>
                                    <input type="button" id="codebtn" value="获取验证码"/>
                                </p>
                                <p>
                                    <input type="password" id="pwd" name="pwd" placeholder="请设置6-18位数字、字母组合密码" maxLength="18"/>
                                </p>
                                <p>
                                    <input type="password" id="repwd" name="repwd" placeholder="请重新输入密码" maxLength="18"/>
                                </p>
                                <p style={{marginBottom:10}}>
                                    <input type="text" id="nickname" name="nickname" placeholder="请输入昵称" maxLength="18"/>
                                </p>
                                <p style={{marginBottom:0}}>
                                    <input type="radio" id="man" name="sex" defaultValue="男" checked/>男
                                    &nbsp;&nbsp;&nbsp;&nbsp;
                                    <input type="radio" id="woman" name="sex" defaultValue="女"/>女
                                </p>
                                <p style={{marginBottom:0}}><input type="checkbox" value="proto" id="proto"/>我已阅读并同意<Link to="/company/regulation">《乐活旅游服务条款》</Link></p>
                                <p>
                                    <input type="button" id="registbtn" value="确认注册"/>
                                </p>
                            </form>
                        </div>
                    </div>
                </div>
                {/*内容结束*/}
                {/*底部开始*/}
                <div className="footer">Copyright © 2005-2018 版权所有 常州乐活旅游投资有限公司 苏ICP备15003740号</div>
                {/*底部结束*/}
            </div>
        );
    }
}


