import React from "react";
import {Link} from "react-router-dom";
import './../../css/header.css';
import logo from './../../images/logo.png';
import Cookies from 'js-cookie';
export default class Header extends React.Component{
    constructor(){
        super();
        this.state={
            name:Cookies.get('name')
        }
    }
    goout(){
        Cookies.remove('name');
        this.setState({name:undefined});
        this.props.val.props.history.push(this.props.path);
    }
    render(){
        const name=this.state.name;
        return(
            <div id="header">
                <div className="w">
                    <div className="logo">
                        <Link to="/">
                            <img src={logo} alt=""/>
                            <h1>乐活旅行</h1>
                        </Link>
                    </div>
                    <div className="navlist">
                        <Link to="/company">消费者帮助</Link>
                        <Link to="/company/download">下载APP</Link>
                    </div>
                    <div className="login">
                        {
                            name!==undefined?(
                                <span>
                                    <Link to="/personal">个人中心</Link>
                                    <a href="javascript:;" onClick={this.goout.bind(this)} style={{marginTop:-5,verticalAlign:"middle"}}>退出</a>
                                </span>):
                                (<span>
                                    <Link to="/login">登录</Link>
                                    <Link to="/regist">注册</Link>
                                </span>)
                        }

                    </div>
                </div>
            </div>
        );
    }
}


