//热门城市
import React from 'react';
import Hometitle from './hometitle';
import {Link} from 'react-router-dom';
import './../../css/hotcity.css';
export default class Hotcity extends React.Component{
    constructor(){
        super();
        this.state={
            hotcity:""
        };
    }
    componentWillMount(){
        fetch("http://admin9.lohas-travel.com/api/index/get_hotcity",{method:"GET"})
            .then((res)=>res.json())
            .then((res)=>this.setState({hotcity:res.data}));
    }
    render(){
        let hotcity=this.state.hotcity;
        let cityname=[];
        let cityimg=[];
        if(hotcity.length){hotcity.map(item=>{cityname.push(item.name);cityimg.push(item.image);});}
        return(
            <div id="hotcity" className="w">
                <Hometitle big="热门城市" small="HOT&nbsp;&nbsp;&nbsp;&nbsp;CITIES"/>
                <div className="pics">
                    <div className="item">
                        <p className="big">
                            <Link to={"/hotel/"+cityname[0]} style={{backgroundImage:"url("+cityimg[0]+")"}}>{cityname[0]}</Link>
                        </p>
                        <p>
                            <Link to={"/hotel/"+cityname[1]} style={{backgroundImage:"url("+cityimg[1]+")"}}>{cityname[1]}</Link>
                        </p>
                        <p>
                            <Link to={"/hotel/"+cityname[2]} style={{backgroundImage:"url("+cityimg[2]+")"}}>{cityname[2]}</Link>
                        </p>
                        <p className="big">
                            <Link to={"/hotel/"+cityname[3]} style={{backgroundImage:"url("+cityimg[3]+")"}}>{cityname[3]}</Link>
                        </p>
                    </div>
                    <div className="item">
                        <p className="small">
                            <Link to={"/hotel/"+cityname[4]} style={{backgroundImage:"url("+cityimg[4]+")"}}>{cityname[4]}</Link>
                        </p>
                        <p className="small">
                            <Link to={"/hotel/"+cityname[5]} style={{backgroundImage:"url("+cityimg[5]+")"}}>{cityname[5]}</Link>
                        </p>
                        <p className="small">
                            <Link to={"/hotel/"+cityname[6]} style={{backgroundImage:"url("+cityimg[6]+")"}}>{cityname[6]}</Link>
                        </p>
                        <p className="small">
                            <Link to={"/hotel/"+cityname[7]} style={{backgroundImage:"url("+cityimg[7]+")"}}>{cityname[7]}</Link>
                        </p>
                        <p className="small">
                            <Link to={"/hotel/"+cityname[8]} style={{backgroundImage:"url("+cityimg[8]+")"}}>{cityname[8]}</Link>
                        </p>
                        <p className="small">
                            <Link to={"/hotel/"+cityname[9]} style={{backgroundImage:"url("+cityimg[9]+")"}}>{cityname[9]}</Link>
                        </p>
                    </div>
                </div>
            </div>
        );
    }
}


