import React from 'react';
import payment from './../../images/payment.png';
import paycode from './../../images/paycode.png';
export default class Payment extends React.Component{
    render(){
        return(
            <div id="payment">
                <h2>支付信息</h2>
                <img src={payment} alt=""/>
                <h2>银行信息</h2>
                <p>账户名称：常州乐活旅游投资有限公司</p>
                <p>账号：1105039809000041568</p>
                <p>开户行：中国工商银行常州科教城科技支行</p>
                <h2>支付宝及微信</h2>
                <p>支付宝账号：lohaspay@lohas-travel.com</p>
                <img src={paycode} alt="" style={{width:150,marginLeft:20}}/>
                <p>微信支付账号：1292184101</p>
            </div>
        );
    }
}


